footer {
	background-color: #212121;
	color: white;

	padding: 50px 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

footer > section {
	margin-top: 20px;
}

.icon {
	color: #FFF;
	font-size: xx-large;
	margin: 20px;
}

.fa-facebook:hover {
	color: #4267B2;
}

.fa-linkedin:hover {
	color: #0077b5;
}

.fa-twitter:hover {
	color: #1DA1F2;
}

.fa-youtube:hover {
	color: #FF0000;
}

