#blogs {
    display: flex;
    flex-direction: column;

    margin: 100px 3%;
}

#blogs > h3 {
    align-self: center;
}

#blogs > hr {
    margin: 30px;
    width: 100px;
    align-self: center;
}

#blog {
    display: flex;
    margin: 10px;
}

#blog-img {
    width: 180px;
    height: 120px;
    margin-right: 20px;

    background-color: blueviolet;
}

@media only screen and (max-width: 600px) {
    #blogs {
        align-items: center;
    }
}