#projects {
    /* width: 100%; */
    /* background-color: aquamarine; */

    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    /* align-items: center; */
    text-align: center;

    margin: 100px 3%;
}

#projects > hr {
    margin: 30px;
    width: 100px;
    align-self: center;
}

#row {
    margin: 10px;
    display: flex;
    justify-content: space-evenly;
}

#project {
    width: 300px;
    height: 200px;
    background-color: bisque;
    margin: 10px;
}

@media only screen and (max-width: 600px) {
    #projects {
        align-items: center;
    }

    #row {
        flex-direction: column;
    }
}