#about-me {
    height: auto;

    display: flex;
    justify-content: space-evenly;
    align-items: center;
    
    padding-top: 50px;
    background-color: rgba(204, 204, 204, 0.3);;
    margin: 100px 0px;
}

#loc {
    width: 1px;
    height: 400px;
    background-color: black;
}

/* EXPERTIES */
.experties {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.experties > hr {
    width: 50px;
    margin-top: 20px;
}

#sub-experties {
    height: 400px;
    width: 300px;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    /* align-items: center; */
    text-align: center;
}

.FaCode, .FaAndroid {
    font-size: 80px;
}

#icon {
    font-size: larger;
    margin: 6px;
}

/* SKILLS */
.skills {
    
}
.skills > hr {
    width: 50px;
    margin-top: 20px;
}

#sub-skills {
    height: 400px;
    width: 300px;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

@media only screen and (max-width: 700px) {
    #about-me {
        flex-direction: column;
    }

    #loc {
        width: 60%;
        height: 1px;
        margin: 50px;
    }

    .experties {
        width: 100%;
    }

    #sub-experties {
        flex-direction: row;
        height: auto;
        width: 100%;

        margin: 20px 0px;
    }

    .skills {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #sub-skills {
        height: 300px;
    }
}