#contact {
    background-color: #121212;
    color: #FFFFFF;
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 80px 3%;
}

#contact > h1{
    align-self: center;
}

#contact > hr {
    margin: 30px;
    width: 100px;
    align-self: center;
}

form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	width: 40%;
}

input[type='text'],
textarea {
	width: 100%;
	padding: 12px 20px;
	margin: 8px 0;

	border: 1px solid #ccc;
	box-sizing: border-box;
	border-radius: 0.2rem;

	outline: none;

	transition: transform 0.3s;
}

input[type='text']:focus,
textarea:focus {
	transform: scale(1.02);
}

form > button {
	width: 100%;
	padding: 10px 0;
	margin-top: 5px;
    
	border-radius: 0.2rem;
}

@media only screen and (max-width: 790px) {
	form {
		width: 80%;
	}
}